@import "src/theme/colors.sass"
.app
  cursor: none

  .cursor
    position: fixed
    width: 10px
    height: 10px
    background-color: $special
    border-radius: 50%
    z-index: 1000
    pointer-events: none
    transition: transform ease-out 10ms

  .stalker
    position: fixed
    margin-top: -10px
    margin-left: -10px
    width: 30px
    height: 30px
    border: .7px solid $special
    border-radius: 50%
    z-index: 999
    pointer-events: none
    transition: transform ease-out 120ms

@media screen and (max-width: 460px)
  .app
    .cursor
      display: none
    .stalker
      display: none